.container {
  z-index: 10;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: #012e46cc;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  display: flex;
  position: fixed;
}

.gallery {
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  place-items: center;
  display: grid;
  position: relative;
  top: 70px;
}

img {
  width: 380px;
  height: 280px;
  overflow: hidden;
}

input {
  border-radius: 13px;
  padding: 7px 15px;
}

.search-button {
  color: #012e46;
  background-color: #0000;
  border: #0000;
  position: relative;
  right: 35px;
}

.search-button:hover {
  cursor: pointer;
  text-shadow: 0 0 10px #012e46, 0 0 40px #012e46, 0 0 80px #012e46;
  border-color: #012e46;
  transition: all .5s;
}

.btn-loadmore {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  top: 55px;
  bottom: 20px;
}

.load-more {
  color: #012e46;
  background-color: #0000;
  border: #0000;
  border-radius: 15px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 5px 15px;
  font-size: 15px;
  font-weight: bold;
  text-decoration: none;
  transition: all .2s;
  overflow: hidden;
}

.load-more:hover {
  color: #fff;
  cursor: pointer;
  background-color: #012e46;
  transition: all .5s;
}

.info-item {
  margin: 0;
}

.photo-card {
  height: 330px;
}

.info {
  color: #012e46;
  text-align: center;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 15px;
  margin-top: 10px;
  display: flex;
}

